.ant-menu-horizontal {
	position: relative;
	background-color: transparent;
	color: #737f92;
	font-weight: bold;
	margin-bottom: 1rem;
	display: flex;
	justify-content: space-around;
	border: none;
}

.ant-menu-horizontal::before {
	position: absolute;
	content: '';
	width: 100%;
	height: 100%;
	bottom: -1rem;
	border-bottom: 1px solid #737f92;
}

.airdrop-table-wrapper tr {
	background-color: #2b3139;
}
